<template>
  <div class="d-flex flex-column align-items-center">
    <br />
    <h1 class="my-5">Edici√≥n de cita üõ†</h1>

    <form
      class="d-flex flex-column align-items-center"
      @submit.prevent="pedirCita(cita)"
    >
      <div id="form" class="container px-5 pb-3">
        <h3 class="mt-3">Cita üìù</h3>
        <div class="d-flex align-items-center">
          <label for="autor" class="mx-2">Tatuador</label>
          <select
            name="autor"
            class="form-control"
            v-model="cita.tatuador"
            id="autor"
          ></select>
        </div>

        <div class="d-flex align-items-center">
          <label class="m-2" for="date">Fecha</label>
          <input
            type="date"
            id="date"
            class="form-control my-2"
            v-model="cita.fechaCita"
            required
          />
        </div>
        <div class="input-group mb-2 d-flex align-items-center">
          <label class="m-2" for="inlineFormInputGroup">Cliente</label>
          <div class="input-group-prepend">
            <span id="apend" class="input-group-text my-2">@</span>
          </div>
          <input
            type="text"
            class="form-control my-2"
            id="inlineFormInputGroup"
            placeholder="Cliente"
            v-model="cita.cliente"
            required
          />
        </div>
        <div class="d-flex align-items-center">
          <label for="tatuaje" class="mx-2">Tatuaje</label>
          <input
            name="tatuaje"
            type="text"
            class="form-control my-2"
            placeholder="Tatuaje"
            v-model="cita.tatuaje"
            required
          />
        </div>
        <div class="input-group mb-2 d-flex flex-column align-items-center">
          <div class="d-flex align-items-center">
            <label class="m-2" for="autorizacion">¬øAutorizado?</label>
            <input
              type="checkbox"
              class="m-2"
              id="autorizacion"
              placeholder="Username"
              v-model="cita.autorizado"
            />
            <div v-if="cita.autorizado">S√≠</div>
            <div v-else>No</div>
          </div>

          <div class="input-group d-flex align-items-center">
            <div class="input-group-prepend">
              <span id="apend" class="input-group-text">‚Ç¨</span>
            </div>
            <input
              type="text"
              class="form-control my-2"
              id="inlineFormInputGroup"
              placeholder="Precio"
              v-model="cita.precio"
              required
            />
          </div>
        </div>
      </div>

      <b-button id="enviar" pill class="my-4 d-block w-100" type="submit">
        Editar
      </b-button>
    </form>

    <div class="container">
      <b-alert
        :show="dismissCountDown"
        :variant="mensaje.color"
        @dismissed="dismissCountDown = 0"
        @dismiss-count-down="countDownChanged"
      >
        <p>{{ mensaje.texto }}</p>
        <b-progress
          :variant="mensaje.color"
          :max="dismissSecs"
          :value="dismissCountDown"
          height="4px"
        ></b-progress>
      </b-alert>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      citas: [],
      dismissSecs: 5,
      dismissCountDown: 0,
      showDismissibleAlert: false,
      mensaje: {
        color: '',
        texto: '',
      },
      cita: {
        tatuador: '',
        cliente: '',
        fechaCita: '',
        precio: '',
        autorizado: Boolean,
        tatuaje: '',
      },
      autores: [],
    }
  },
  created() {
    if (
      localStorage.getItem('tatuador') != 'true' ||
      location.href.split('?id=')[1] == undefined
    ) {
      this.$router.go(-1)
    } else {
      const id = location.href.split('?id=')[1]
      this.axios
        .get('citas/listar/' + id)
        .then((res) => {
          this.cita = res.data
          this.cita.fechaCita = res.data.fechaCita.substring(0, 10)
        })
        .catch((e) => {
          console.log(e.response)
        })
    }
  },
  mounted() {
    this.axios
      .get('/usuarios/tatuadores')
      .then((res) => {
        this.autores = res.data
        this.insertarTatuadores(this.autores)
      })
      .catch((e) => {
        console.log(e)
      })
  },
  methods: {
    alertaTrue() {
      this.mensaje.color = 'primary'
      this.mensaje.texto = 'Cita editada con exito! üëå'
      this.showAlert()
    },
    alertaFalse() {
      this.mensaje.color = 'warning'
      this.mensaje.texto = 'Error :('
      this.showAlert()
    },
    pedirCita(item) {
      this.axios
        .put('citas/actualizar/' + item._id, item)
        .then((res) => {
          this.cita.tatuador = res.data.tatuador
          this.cita.cliente = res.data.cliente
          this.cita.fechaCita = res.data.fechaCita
          this.cita.precio = res.data.precio
          this.cita.autorizado = res.data.autorizado
          this.cita.tatuaje = res.data.tatuaje
          this.alertaTrue()
        })
        .catch((e) => {
          console.log(e)
        })
    },
    insertarTatuadores(tatuadores) {
      var autores = document.getElementById('autor')
      var opcion = document.createElement('option')
      opcion.value = ''
      opcion.innerHTML = 'Desconocido'
      autores.appendChild(opcion)
      for (var i = 0; i < tatuadores.length; i++) {
        opcion = document.createElement('option')
        opcion.value = tatuadores[i]._id
        opcion.innerHTML = tatuadores[i].nombre
        if (tatuadores[i]._id == this.cita.tatuador) {
          opcion.setAttribute('selected', '')
        }
        autores.appendChild(opcion)
      }
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown
      if (this.mensaje.color == 'primary') {
        setTimeout(() => {
          window.location.href = '/citas'
        }, 5000)
      }
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs
    },
  },
}
</script>

<style>
#form {
  background-color: #82a5c7;
  color: #243241;
  border-radius: 10px;
}
select {
  background-color: #4e647a !important;
  color: #82a5c7 !important;
}
input {
  background-color: #4e647a !important;
  color: #82a5c7 !important;
}
span#apend {
  background-color: #243241 !important;
  color: #82a5c7 !important;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
#enviar {
  background-color: #82a5c7;
  color: #243241;
}
</style>
